<script setup async>
import { ref } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useSettingPlansBillings } from 'store/pinia/setting/plans-billings'
import { useSetting } from 'store/pinia/setting'
import { useRouter } from 'vue-router'

import SettingPlanBillingVerifyPayment from 'components/Features/Setting/SettingPlanBilling/SettingPlanBillingVerifyPayment.vue'
import SettingPlanBillingPackages from 'components/Features/Setting/SettingPlanBilling/SettingPlanBillingPackages.vue'
import SettingPlanBillingResubscribe from 'components/Features/Setting/SettingPlanBilling/SettingPlanBillingResubscribe.vue'
import SettingPlanBillingResubsLocation from 'components/Billing/ResubsLocation/index.vue'
import { ElMessage } from 'element-plus'

const router = useRouter()
const storeSetting = useSetting()
const store = useSettingPlansBillings()
const showResubs = ref(false)
const periodType = ref()
const refPackages = ref()
const periodes = [{
  id: 'monthly',
  label: 'Monthly'
}, {
  id: 'yearly',
  label: 'Yearly'
}]


const checkGateStripePortal = (url) => {
  try {
    const targetUrl = new URL(url)
    const isStripePortal = targetUrl.origin.includes('stripe')

    if (isStripePortal) {
      return { srtipe: true, url }
    } else {
      return { srtipe: false, url: targetUrl.pathname }
    }
  } catch (e) {
    return { srtipe: false, url: '/setting' }
  }
}

const onLoad = async () => {
  await tryCatchRequest(
    storeSetting.billing,
    () => storeSetting.$getUrlBilling()
  )

  const url = storeSetting.billing?.result?.url

  if (url) {
    const result = checkGateStripePortal(url)
    storeSetting.billing.isLoading = true

    if (result.srtipe ) {
      window.location.href = result.url
    } else if (result.url !== '/setting') {
      storeSetting.billing.isLoading = false
      await tryCatchRequest(
        store.packages,
        () => store.$getBillingPackages()
      )
    } else {
      router.push({
        name: '/setting'
      })
    }
  }
}
const onReactive = async () => {
  showResubs.value = true
}
const onResubs = async (data) => {
  await tryCatchRequest(
    store.reSubscribe,
    () => store.$planCheckout({
      price_id: store.packages.selectedPlan.price_id,
      total_location: typeof data === 'object' ? data?.total_selected_location : data
    }).then(() => {
      store.packages.selectedPlan = undefined
      window.location.href = store.reSubscribe.result
    }).catch(() => {
      ElMessage({
        type: 'error',
        message: `An error occurred during checkout. Please reach out to our customer support team for assistance.`
      })
    })
  )
}

await onLoad()

periodType.value = ['year', 'annual'].includes(store?.packages?.result?.current_interval) ? 'yearly' : 'monthly'

</script>

<template>
  <div v-if="!storeSetting.billing.isLoading" class="mt-6 space-y-10">
    <div class="flex items-center justify-between">
      <h5 class="font-medium">Plan & Blling</h5>
      <SettingPlanBillingVerifyPayment />
    </div>
    <div class="space-y-6">
      <div v-if="store.unpaid || store.canceled">
        <div class="flex items-center bg-red-lighten text-red-04 py-2 px-4 rounded-lg gap-4">
          <i class="material-icons">info</i>
          <p>
            To continue using reusely.com, please update your billing details. Once your payment is
            successfully made,
            click "Verify Payment" to confirm.
          </p>
        </div>
      </div>
      <div>
        <label class="font-medium block text-teal-04">
          Compare Plans
        </label>
        <RadioGroup
          v-model="periodType">
          <RadioButton
            v-for="period in periodes"
            :key="period.id"
            :label="period.id">
            {{ period.label }}
          </RadioButton>
        </RadioGroup>
      </div>
      <SettingPlanBillingPackages
        ref="refPackages"
        :period="periodType"
      />
    </div>
    <SettingPlanBillingResubscribe
      @reactive="onReactive"
    />
    <SettingPlanBillingResubsLocation
      v-model="showResubs"
      @subscribe="onResubs"
    />
  </div>
  <div v-else v-loading="true" class="h-96" />
</template>
