<script setup>
import { onMounted, defineProps, defineExpose, computed } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useSettingPlansBillings } from 'store/pinia/setting/plans-billings'

import plansDetail from 'constants/packages'

import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import { ElMessage } from 'element-plus'

const props = defineProps({
  period: {
    type: String,
    default: 'monthly' 
  }
})
const packages = computed(() => {
  if (!store.packages?.result || !props?.period) return []

  return store.packages?.result[props?.period].map(i => ({
    ...plansDetail.find(plan => i.package_id === plan.id),
    ...i
  })).sort((x, y) => x.sort - y.sort)
})
const store = useSettingPlansBillings()

const onLoad = async () => {
  await tryCatchRequest(
    store.packages,
    () => store.$getBillingPackages()
  )
}
const onSubscribe = async (plan) => {
  store.packages.selectedPlan = plan
  if (store.canceled) {
    store.reSubscribe.modal = true

    return undefined
  }

  if (plan.current_price_id || store.unpaid) {
    const newWindow = window.open()
    await tryCatchRequest(
      store.unpaidBilling,
      () => store.$unpaidBilling().then(() => {
        if(!store.unpaidBilling?.result?.invoice) {
          return ElMessage({
            type: 'error',
            message: 'An error occurred during checkout. Please reach out to our customer support team for assistance. [No Invoice URL]'
          })
        }
        store.packages.selectedPlan = undefined
        newWindow.location = store.unpaidBilling?.result?.invoice
      })
    )

    return undefined
  }

  await tryCatchRequest(
    store.reSubscribe,
    () => store.$planCheckout({
      price_id: store.packages.selectedPlan.price_id
    }).then(() => {
      store.packages.selectedPlan = undefined
      window.location.href = store.reSubscribe.result
      // window.open(store.reSubscribe.result, '_blank', 'noreferrer noopener')
    }).catch(() => {
      ElMessage({
        type: 'error',
        message: `An error occurred during checkout. Please reach out to our customer support team for assistance.`
      })
    })
  )
}

onMounted(async () => {
  if (!!store?.packages?.result) return
  onLoad()
})

defineExpose({
  currentInterval: store?.packages?.result,
  onLoad
})
</script>
<template>
  <div
    v-if="!!store.packages?.result"
    class="flex flex-wrap">
    <div
      v-for="(packageItem, n) in packages"
      :key="n"
      class="sm:w-1/2 md:w-1/3 py-4 px-2">
      <Card class="h-full relative">
        <template #default>
          <div class=" space-y-8">

            <!-- Nama and Desc -->
            <div class="space-y-4">
              <div class="flex items-center gap-x-2">
                <h5 class="text-xl font-medium">
                  {{ packageItem.name }}
                </h5>
                <div
                  v-if="packageItem.current_price_id"
                  class="px-3 py-0.5 bg-teal-04 rounded-full text-white text-sm">
                  Current Plan
                </div>
              </div>
              <p class="text-grey-03">
                {{ packageItem.description }}
              </p>
            </div>

            <!-- Price -->
            <div>
              <h6 class="text-5xl font-medium inline-block">
                {{ packageItem.price }}
              </h6>
              <span
                v-if="props.period === 'monthly'"
                class="ml-2">
                per location/monthly
              </span>
              <span
                v-else
                class="ml-2">
                savings per year, per location
              </span>
            </div>

            <!-- Saving -->
            <div
              v-if="props.period !== 'monthly' && packageItem.saving_amount !== '$0'"
              class="bg-green-01 text-green-04 rounded-full px-4 py-1 inline-block">
              <span class="font-semibold">{{ packageItem.saving_amount }}</span> savings per year, per location
            </div>
            <div v-else />

            <!-- Feature -->
            <div>
              <p class="font-medium text-teal-04">
                {{ packageItem.feature_term }}
              </p>
              <div
                :class="[
                  packageItem.features?.length > 5 && 'columns-2',
                  'space-y-1'
                ]">
                <div
                  v-for="(feature, m) in packageItem.features"
                  :key="`${n}_${m}`">
                  <div class="flex items-center gap-x-3">
                    <span class="material-icons text-teal-04">check_circle</span>
                    {{ feature }}
                  </div>
                </div>
              </div>
            </div>

          </div>
        </template>
        <template #footer>
          <div>
            <Button
              v-bind="{
                color: 'teal',
                ...(!packageItem.current_price_id && { view: 'outlined' })
              }"
              block
              :loading="store.unpaidBilling.isLoading || store.reSubscribe.isLoading"
              @click="onSubscribe(packageItem)">
              {{
                (store.canceled || store.unpaid) && packageItem.current_price_id
                  ? 'Resubscribe with current plan'
                  : 'Subscribe'
              }}
            </Button>
          </div>
        </template>
      </Card>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.columns-2 {
  column-count: 2;
  column-gap: 2rem; /* Sesuai dengan gap yang kamu mau */
}
:deep(.card__content) {
  border-bottom: 0px !important;
}
</style>