<script setup>
import { ref } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useSettingPlansBillings } from 'store/pinia/setting/plans-billings'


import Button from 'bbUi/components/button/button.vue'

const store = useSettingPlansBillings()

const onVerify = async () => {
  await tryCatchRequest(
    store.verifyPayment,
    () => store.$getVerifyPayment().then(() => {
      if (
        store?.verifyPayment.result?.is_unpaid_billing === 0 &&
        store?.verifyPayment.result?.is_trial_end === 0
      ) {
        window.location.href = '/dashboard'
      }
    })
  )
}

</script>

<template>
  <Button
    :disabled="store.verifyPayment.isLoading"
    :loading="store.verifyPayment.isLoading"
    color="teal"
    @click="onVerify">
    <div class="flex items-center justify-center gap-x-2">
      <span
        v-if="!store.verifyPayment.isLoading"
        class="material-icons">
        refresh
      </span>
      Verify Payment
    </div>
  </Button>
</template>
