<template>
  <form @submit.prevent>
    <div class="border border-grey-01 rounded-lg p-4 space-y-8">
      <div class="space-y-2">
        <div class="flex items-center gap-x-4 mb-6">
          <div class="bg-teal-04 w-6 h-6 text-white flex items-center justify-center rounded-full">1</div>
          <p class="font-semibold">Resubscribe Location</p>
        </div>
        <div class="lg:mx-10">
          <div>
            <label class="text-sm">Location</label>
            <MultiSelect v-model="form.locations" placeholder="Choose Location" :loading="loading.location"
              :disabled="loading.location" :options="data.locations" :multiple="true" :hide-selected="false"
              :show-labels="false" :show-no-results="true" :show-no-options="true" :searchable="false" track-by="id"
              label="name" @remove="handleRemoveLocation" @select="handleSelectLocation" required>
              <template #option="props">
                <div class="flex items-center gap-x-4">
                  <input v-model="props.option.checked" type="checkbox"
                    class="w-5 h-5 text-teal-04 bg-teal-04 border-gray-300 rounded"
                    style="accent-color: rgba(66, 66, 66, 0.11);" />
                  <div>
                    <p class="text-sm text-grey-03 mb-1">
                      {{ props.option.name }}
                    </p>
                    <p>{{ props.option.address_text }}</p>
                  </div>
                </div>
              </template>
            </MultiSelect>
          </div>
        </div>
      </div>
      <div :class="[!form?.locations?.length ? 'opacity-50' : 'opacity-100']" class="space-y-2">
        <div class="flex items-center gap-x-4 mb-6">
          <div class="bg-teal-04 w-6 h-6 text-white flex items-center justify-center rounded-full">2</div>
          <p class="font-semibold">Transfer Data</p>
        </div>
        <div v-if="!!form?.locations?.length" class="lg:mx-10 space-y-6">
          <div v-if="!!unselecLocation" class="bg-orange-01 text-orange-04 rounded-md px-4 py-2">
            <div class="flex items-center gap-x-4">
              <i class="material-icons">info</i>
              <p>
                <b>Important: {{ unselecLocation }} locations are currently unselected</b>. These locations will be
                permanently deleted; however, you must transfer their data to another location to proceed. Select the
                locations you want to transfer the data to below.
              </p>
            </div>
          </div>
          <div class="border border-grey-01 border-opacity-50 space-y-6">
            <Table v-loading="loading.transfer" :data="locationToTransfer">
              <TableColumn prop="name" label="Location Name" />
              <TableColumn prop="address_text" label="Address" />
              <TableColumn prop="email" label="Email" />
              <TableColumn prop="phone" label="Phone Number" />
              <TableColumn label="Transfer Location" width="350">
                <template #default="{ row }">
                  <Select v-model="row.transfer" by="id" name="name" placeholder="Choose Location"
                    :items="form.locations" block no-border max-height="200px"
                    @change="handleSelectTransferLocation(row)">
                    <template #item="{ item }">
                      <div class="space-y-2">
                        <span class="w-full block text-sm font-semibold">
                          {{ item.name }}
                        </span>
                        <span class="w-full text-xs">
                          {{ item.address_text }}
                        </span>
                      </div>
                    </template>
                  </Select>
                </template>
              </TableColumn>
            </Table>
            <div v-if="form?.locations?.length > 10" class="px-4 pb-4">
              <Pagination v-model:page="query.page" v-model:limit="query.limit" />
              <!-- :data="tableOptions" -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import { computed, defineComponent, onMounted } from 'vue'
import { useResubsLocationForm } from 'composable/billing'

import MultiSelect from 'vue-multiselect'
import Select from 'components/Select.vue'
import Pagination from 'components/Pagination.vue'

export default defineComponent({
  name: 'ResubsLocationForm',
  components: {
    MultiSelect,
    Pagination,
    Select
  },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    mainLoading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'continue'],
  setup(props, ctx) {
    const {
      loading,
      data,
      query,
      showLocation
    } = useResubsLocationForm()
    const form = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const locationToTransfer = computed(() => {
      const selected = form.value?.locations?.map(location => location.id)
      if (!!selected.length) {
        return [...data.locations].filter(location => !selected.includes(location.id))
      }

      return data.locations
    })
    const unselecLocation = computed(() => {
      const locations = data?.locations?.length || 0
      const selected = form.value?.locations?.length || 0

      return locations - selected
    })
    const handleRemoveLocation = (option) => {
      data.locations = data.locations.map(location => {
        return ({
          ...location,
          transfer: null,
          checked: option.id === location.id ? false : location.checked
        })
      })
      option.checked = false
      return option
    }
    const handleSelectLocation = (option) => {
      option.checked = true
      return option
    }
    const handleSelectTransferLocation = (row) => {
      form.value.transfers = [...locationToTransfer.value].filter(item => ![null, undefined].includes(item.transfer))
      return row
    }
    const handleUpdateCheck = (e, option) => {
      if (!!option?.checked) return handleRemoveLocation(option)

      handleSelectLocation(option)
    }

    onMounted(async () => {
      await showLocation()
      // if (data?.locations?.length === 1) {
      //   const option = [...data.locations].shift()
      //   form.value.locations = option
      //   handleSelectLocation(option)
      // }
    })

    return {
      loading,
      data,
      query,
      form,
      locationToTransfer,
      showLocation,
      unselecLocation,
      handleUpdateCheck,
      handleRemoveLocation,
      handleSelectLocation,
      handleSelectTransferLocation
    }
  }
})
</script>
