<template>
  <Modal v-model="show" size="lg" hide-header>
    <template #default>
      <div class="py-4 space-y-6">
        <div class="space-y-6">
          <div class="flex items-center justify-center">
            <img src="@/assets/icon/attention-blue.svg" alt="Info Icon">
          </div>
          <p class="text-center text-lg font-semibold">Before you continue</p>
          <div
            v-if="locations && locations > 1"
            class="bg-blue-01 text-blue-04 rounded-lg px-4 py-2">
            <div class="flex gap-x-4">
              <p class="text-sm">
                <span class="font-semibold">
                  You have
                  {{ `${locations} locations` }}
                </span>.
                Choose the locations you want to include in your billing update.
              </p>
            </div>
          </div>
        </div>
        <div v-loading="loading">
          <ResubsLocationForm
            ref="resubsLocationForm"
            v-model="form"
            :main-loading="loading"
          />
        </div>
      </div>
    </template>
    <template #footer>
      <div class="flex items-center justify-end gap-x-4">
        <a
          href="#"
          class="hover:opacity-50 hover:bg-grey-01 px-6 py-3.5 rounded-md"
          @click.prevent="cancelRemoveLocation">
          Cancel
        </a>
        <Button
          color="teal"
          :loading="loading"
          :disabled="disabledButton"
          @click="continueRemoveLocation">
          Continue
        </Button>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, computed, ref } from 'vue'
import { useResubsLocation } from 'composable/billing'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import ResubsLocationForm from 'components/Billing/ResubsLocation/Form/index.vue'

export default defineComponent({
  name: 'ResubsLocation',
  components: {
    Modal,
    Button,
    ResubsLocationForm
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'subscribe'],
  setup(props, ctx) {
    const resubsLocationForm = ref()
    const { form, loading, removeLocation } = useResubsLocation()
    const show = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const locations = computed(() => resubsLocationForm?.value?.data?.locations?.length || 0 )
    const disabledButton = computed(() => {
      if (!form?.locations?.length) return true
      if (form?.transfers?.length < resubsLocationForm?.value?.unselecLocation) return true
      return false
    })
    const continueRemoveLocation = async () => {
      if(form?.locations?.length < locations.value) await removeLocation()
      ctx.emit('subscribe', {
        total_selected_location: form?.locations?.length
      })
      cancelRemoveLocation()
    }
    const cancelRemoveLocation = () => {
      show.value = false
      form.locations = []
      form.transfers = []
    }

    return {
      show,
      form,
      loading,
      locations,
      disabledButton,
      resubsLocationForm,
      removeLocation,
      continueRemoveLocation,
      cancelRemoveLocation
    }
  }
})
</script>
