import { computed } from 'vue'
import { defineStore } from 'pinia'
import store from 'store'
import request from 'utils/request'

export const useSettingPlansBillings = defineStore('settingPlansBilling', {
  state: () => ({
    packages: {
      isLoading: false,
      result: undefined,
      selectedPlan: undefined
    },
    verifyPayment: {
      isLoading: false,
      result: undefined
    },
    reSubscribe: {
      modal: false,
      isLoading: false,
      result: undefined
    },
    unpaidBilling: {
      isLoading: false,
      result: undefined
    }
  }),
  getters: {
    trial() {
      return store?.state?.user?.isTrialEnd === 0 ?? false
    },
    canceled() {
      return store?.state?.user?.me?.subscription_status == 3 ?? false
    },
    unpaid() {
      return store?.state?.user?.isUnpaidBilling !== 0 ?? false
    }
  },
  actions: {
    async $getStripePortal() {
      const response = await request({
        url: `/v1/pos/billing/customer/portal`,
        method: 'get'
      })
      this.stripePortal.result = response
    },
    async $getBillingPackages() {
      const response = await request({
        url: `/v1/pos/billing/package`,
        method: 'get'
      })
      this.packages.result = response
    },
    async $getVerifyPayment() {
      const response = await request({
        url: '/v1/login-status',
        method: 'get'
      })
      this.verifyPayment.result = response
    },
    async $planCheckout(payload) {
      const response = await request({
        url: `/v1/pos/billing/package/checkout`,
        method: 'post',
        data: payload
      })
      this.reSubscribe.result = response
    },
    async $unpaidBilling() {
      const response = await request({
        url: `/v1/pos/billing/invoice/unpaid`,
        method: 'post'
      })
      this.unpaidBilling.result = response
    }
  }
})
