<script setup>
import { defineEmits, computed } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useLocations } from 'composable/dashboard'
import { useSettingPlansBillings } from 'store/pinia/setting/plans-billings'

import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Button from 'bbUi/components/button/button.vue'
import { ElMessage } from 'element-plus'

const emits = defineEmits(['reactive'])
const { stateFiltered: locations } = useLocations()
const store = useSettingPlansBillings()

const onCheckout = async (location = 1) => {
  await tryCatchRequest(
    store.reSubscribe,
    () => store.$planCheckout({
      price_id: store.packages.selectedPlan.price_id,
      total_location: typeof location === 'object' ? location?.total_selected_location : location
    }).then(() => {
      store.packages.selectedPlan = undefined
      window.open(store.reSubscribe.result, '_blank', 'noreferrer noopener')
    }).catch(() => {
      ElMessage({
        type: 'error',
        message: `An error occurred during checkout. Please reach out to our customer support team for assistance.`
      })
    })
  )
}
const onCancel = async () => {
  store.reSubscribe.modal = false
}
const onReactive = async () => {
  if (locations.value.total > 1) {
    return emits('reactive')
  }
  await onCheckout()
}

</script>

<template>
  <Modal
    v-model="store.reSubscribe.modal"
    size="xs"
    hide-footer
    hide-header>
    <template #default>
      <div class="px-4 py-8">
        <img
          class="mb-10 mx-auto text-orange-base"
          src="@/assets/icon/warning.svg"
          alt="Success Icon"
        />
        <Alert color="orange" view="smooth">
          <div class="flex">
            <i class="material-icons mr-4">info</i>
            <p
              whitelabel="text-replace"
              data-label="addons host">
              Your Reusely.com subscription has been canceled due to
              either unpaid billing, incorrect billing information, or because you canceled it.
            </p>
          </div>
        </Alert>
        <div
          class="text-grey-04 rounded-lg px-4 py-6 my-4"
          :style="{
            backgroundColor: 'rgba(0,0,0,0.03)'
          }">
          <p class="font-medium">Important actions to take:</p>
          <ul class="text-grey-03 my-4">
            <li>1. When your subscription ends, you will no longer be able to access your account, and your data will be deleted.</li>
            <li>2. If your account is still active, please take this time to download any customer or other data you may need.</li>
          </ul>
          <p class="text-grey-03">Reach out to our support team for additional help.</p>
        </div>
        <div class="flex flex-wrap justify-center text-center mt-6 gap-4">
          <Button
            :disabled="store.reSubscribe.isLoading"
            color="white"
            @click="onCancel">
            Cancel
          </Button>
          <Button
            :loading="store.reSubscribe.isLoading"
            color="teal"
            @click="onReactive">
            Reactive Subscription
          </Button>
        </div>
      </div>
    </template>
  </Modal>
</template>